.banner {
  color: white;
  object-fit: contain;
  height: 550px;
  /* background: rgba(0, 0, 0, 0.4); */

  /* background-image: -webkit-gradient(
    linear,
    left bottom,
    left top,
    from(rgba(0, 0, 0, 0.8)),
    color-stop(60%, rgba(0, 0, 0, 0)),
    to(rgba(0, 0, 0, 0.8))
  ); */
}

.banner_contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}
.banner_title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.8rem;
}
.banner_description {
  width: 45rem;
  line-height: 1.3;
  padding-top: 4rem;
  font-size: 0.8rem;
  max-width: 360px;
  height: 80px;
}
.banner_button {
  cursor: pointer;
  outline: none;
  border: none;
  font-weight: 700;
  border-radius: 0.2vw;
  padding-left: 2rem;
  padding-right: 2rem;
  margin-right: 1rem;
  padding-top: 0.5rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
  color: white;
}

.banner_button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}
.banner--fadebottom {
  height: 23rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}

@media (max-width: 400px) {
  .banner_description {
    position: relative;
    right: 25px;
  }
}
